import React, { Component, Suspense } from "react";
import { Button, CardKelas } from "../../../component";
const Hero = React.lazy(() => import("../../../component/Hero"));
const ChangeKelas = React.lazy(() => import("../../../component/ChangeKelas"));
const About = React.lazy(() => import("../../../component/About"));
const Benefit = React.lazy(() => import("../../../component/Benefit"));

export default class Home extends Component {
  render() {
    const HomeItem = [
      {
        name: "Kelas Hidroponik",
        mentor: "",
        background: "./assets/img/about/hidroponik-about.png",
        to: "/hidroponik",
        disable: false,
        kelasTersedia: "1",
      },
      {
        name: "Kelas Pertanian",
        mentor: "",
        background: "./assets/img/about/pertanian-about.png",
        to: "/pertanian",
        disable: false,
        kelasTersedia: "0",
      },
      {
        name: "Kelas Perkebunan",
        mentor: "",
        background: "./assets/img/about/perkebunan-about.png",
        to: "/perkebunan",
        disable: false,
        kelasTersedia: "0",
      },
    ];
    return (
      <div>
        <Suspense fallback={<div>Loading...</div>}>
          <Hero>
            <Button
              onClick={() => window.location.replace("/#changekelas")}
              color="primary"
              bgcolor="bgsecondary"
            >
              Gabung Sekarang
            </Button>
            <Button
            onClick={() => window.location.replace("/#benefit")}
              color="secondary"
              bgcolor="bgsecondary"
              styles="outline"
            >
              Apa Keuntungannya?
            </Button>
          </Hero>
          <About>
            <Button color="primary" styles="outline">
              Pelajari Lebih Lanjut
            </Button>
          </About>
          <ChangeKelas id={"changekelas"} >
            {HomeItem.map((item) => {
              return (
                <CardKelas
                  bgImg={item.background}
                  disable={item.disable}
                  url={item.to}
                  mentor={item.mentor}
                  kelasTersedia={item.kelasTersedia}
                >
                  {item.name}
                </CardKelas>
              );
            })}
          </ChangeKelas>
          <Benefit id={"benefit"} />
        </Suspense>
      </div>
    );
  }
}
